
.MainMenu {
  position: absolute;
  color: #fff;
}
.MainMenu__Mask {
  background-color: rgba(0,0,0,0.76);
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 8;
}
ul.MainMenu__Sidebar {
  background-color: #3263a3;
  height: 100vh;
  position: relative;
  width: 200px;
  z-index: 9;
}
ul.MainMenu__Sidebar>li {
  border-bottom: 1px solid #4182d6;
}
ul.MainMenu__Sidebar>li>a {
  color: #fff;
  display: block;
  font-size: 1.083rem;
  height: 40px;
  line-height: 40px;
  padding-left: 15px;
  width: 100%;
  text-decoration: none;
}
ul.MainMenu__Sidebar>li.MainMenu__Sidebar--active {
  background-color: #4182d6;
  border-right: 5px solid #fff;
}


