
.NoticeModal {
  position: absolute;
  width: 100%;
  font-size: 1rem;
}
.NoticeModal__Mask {
  background-color: rgba(0,0,0,0.76);
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 8;
}
.NoticeModal__Box {
  background-color: #fff;
  position: fixed;
  margin: -57px 0 0 -155px;
  width: 310px;
