  left: 50%;
  top: 50%;
  text-align: center;
  z-index: 9;
}
.NoticeModal__Text {
  margin-bottom: 12px;
  line-height: 1.5rem;
}
.NoticeModal__Text a {
  color: #333;
  text-decoration: underline;
}
.NoticeModal__Btn {
  background-color: #3263a3;
  border: none;
  color: #fff;
  cursor: pointer;
  padding: 5px;
}


